"use client";

import Image from "next/image";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { useEffect, useState } from "react";
import menuData from "./menuData";

const Header = () => {
  const [navbarOpen, setNavbarOpen] = useState(false);
  const [sticky, setSticky] = useState(false);
  const [openIndex, setOpenIndex] = useState(-1);
  const pathname = usePathname();

  useEffect(() => {
    const handleStickyNavbar = () => {
      setSticky(window.scrollY >= 80);
    };
    window.addEventListener("scroll", handleStickyNavbar);
    return () => window.removeEventListener("scroll", handleStickyNavbar);
  }, []);

  return (
    <header
      className={`header left-0 top-0 z-40 w-full items-center transition ${
        sticky ? "fixed z-[9999] shadow-md backdrop-blur-sm" : "absolute bg-transparent"
      }`}
    >
      <div className="container flex justify-between items-center px-4 py-3">
        <div className="w-60">
          <Link href="/">
            <Image src="/images/logo/logo.svg" alt="logo" width={140} height={30} />
          </Link>
        </div>
        <button
          onClick={() => setNavbarOpen(!navbarOpen)}
          className="lg:hidden p-2 focus:ring-2"
        >
          <span className={`block h-0.5 w-6 bg-black transition ${navbarOpen ? "rotate-45 translate-y-2" : ""}`} />
          <span className={`block h-0.5 w-6 bg-black my-1 transition ${navbarOpen ? "opacity-0" : ""}`} />
          <span className={`block h-0.5 w-6 bg-black transition ${navbarOpen ? "-rotate-45 -translate-y-2" : ""}`} />
        </button>
        <nav
          className={`absolute right-0 w-64 p-4 bg-white shadow-md transition transform lg:static lg:w-auto lg:flex lg:items-center lg:shadow-none lg:p-0 ${
            navbarOpen ? "top-full opacity-100" : "-top-[200%] opacity-0"
          }`}
        >
          <ul className="lg:flex lg:space-x-6">
            {menuData.map((menuItem, index) => (
              <li key={index} className="relative">
                {menuItem.path ? (
                  <Link
                    href={menuItem.path}
                    className={`block px-4 py-2 text-lg ${pathname === menuItem.path ? "text-primary" : "text-black hover:text-primary"}`}
                  >
                    {menuItem.title}
                  </Link>
                ) : (
                  <>
                    <button
                      onClick={() => setOpenIndex(openIndex === index ? -1 : index)}
                      className="flex items-center justify-between w-full px-4 py-2 text-lg text-black hover:text-primary"
                    >
                      {menuItem.title}
                      <span className="pl-2">▼</span>
                    </button>
                    <ul className={`absolute left-0 mt-2 w-48 bg-white shadow-md transition ${openIndex === index ? "block" : "hidden"}`}>
                      {menuItem.submenu?.map((submenuItem, subIndex) => (
                        <li key={subIndex}>
                          <Link
                            href={submenuItem.path || '/'}
                            className="block px-4 py-2 text-black hover:text-primary"
                          >
                            {submenuItem.title}
                          </Link>
                        </li>
                      ))}
                    </ul>
                  </>
                )}
              </li>
            ))}
          </ul>
        </nav>
      </div>
    </header>
  );
};

export default Header;
